<template>
  <div>
    
    <div class="row of-visible">
      <div class="col-sm-6 pt-2">
        <h5 class="fw-none">
          產品內容
        </h5>
      </div>
      <div class="col-sm-6 pt-1 text-right of-visible" v-if="_listings.length > 0">

        <sort-option :_list="_listings"></sort-option>

        <select-option :_listFull="_listings" :_list="_select"></select-option>
        
        <!-- num-order-option></num-order-option -->

      </div>
      <div class="col-sm-6 d-flex justify-content-end of-visible" v-else>
        <div class="right-loading">
          <loading-small></loading-small>
        </div>
      </div>

    </div>
    
    <table class="table m-0">
      <tr class="text-left">
        <td width="6%" class="text-accent">#</td>
        <td width="10%">
          貨品編號
        </td>
        <td width="20%">描述</td>
        <td width="15%">
          <div class="pl-2">
            數量
            <help-icon :help_code="'listing_plus_num_input'"></help-icon>
          </div>
        </td>
        <td width="15%">
          <div class="pl-3">
            單價
            <help-icon :help_code="'listing_plus_price_input'"></help-icon>
          </div>
        </td>
        <td width="9%" class="text-center">
          折扣
          <help-icon :help_code="'listing_plus_discount_input'"></help-icon>
        </td>
        <td width="15%" class="text-center">價格</td>
        
        <td width="10%" class="text-center">選擇</td>
      </tr>
    </table>

  </div>
</template>

<script>
import HelpIcon from '@/components/Modal/Help/HelpIcon.vue'
import tool from '@/common/es/tool'
import NumOrderOption from '@/components/Filter/NumOrderOption.vue'
import SortOption from '@/components/Filter/SortOption.vue'
import SelectOption from '@/components/Filter/SelectOption.vue'

import LoadingSmall from '@/components/Shimmer/LoadingSmall.vue'

export default {
  components: { HelpIcon, NumOrderOption, SortOption, SelectOption, LoadingSmall },
  name: 'ListingPlusTop',
  props: {
    _listings: {
      type: Array,
    },
    _select: {
      type: Array
    }
  },
  data() {
    return {
      isSort: false,
      isAll: false
    }
  },
  methods: {
    sortOption() {
      this._listings.reverse()
      this.isSort = !this.isSort
      if (this.isSort) { tool.showToast('逆序排列') } else { tool.showToast('顺序排列') }
    },
    tipSelect() {
      tool.showToast('双击即可全选与清空选择')
    },
    selectOption() {
      if (!this.isAll) {
        const none = this._listings.filter((e) => {
          return !tool.inList(e.id, this._select)
        })
        
        for (const i of none) {
          this._select.push(i.id)
        }
        this.isAll = true

        tool.showToast('全选')
      } else {
        this._select.splice(1, (this._select.length - 1))
        this.isAll = false

        tool.showToast('移除全选')
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


